
import urlBg from './1.png';
import urlPicture from './picture.jpg';

const winRect = {
    width: window.innerWidth,
    height: window.innerHeight
};

const canvasEl = document.createElement('canvas');
canvasEl.width = winRect.width;
canvasEl.height = winRect.height;
const ctx = canvasEl.getContext('2d');
document.body.appendChild(canvasEl);

const loadImage = (url) => new Promise((resolve, reject) => {
    const image = document.createElement('img');
    image.src = url;
    image.onload = () => resolve(image);
    image.onerror = reject;
});

Promise.all(
    [urlBg, urlPicture].map(loadImage)
).then(([bg, picture]) => {
    ctx.drawImage(bg, 0, 0, bg.naturalWidth / 2, bg.naturalHeight / 2);
    ctx.globalCompositeOperation = 'source-in';
    const rato = picture.naturalHeight / winRect.height;
    const pictureRect = {
        width: picture.naturalWidth / rato,
        height: picture.naturalHeight / rato
    };
    ctx.drawImage(picture, (winRect.width - pictureRect.width) / 2, 0, pictureRect.width, pictureRect.height);
});

